* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

// color pallette
$main-color: darkcyan;
$text-color: azure;
$link-color: rgb(150, 241, 75);
$link-color-hover: rgba(150, 241, 75, 0.61);

//desktop size
$desktop: 700px;



body {
  background-color: $main-color;
  color: $text-color;
  text-align: center;
  #root {
    .App {
      .albums-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-width: 420px;
        .album {
          margin-bottom: 2em;
          border-bottom: 2px solid $text-color;

          h5 {
            margin-bottom: 1.1em;
            font-size: 1.1rem;
            font-weight: 600;
          }
          a {
            font-family: monospace;
            font-size: 1.4rem;
            color: $link-color;
            text-decoration: none;
            display: block;
            padding-bottom: 1em;
            &:hover {
              color: $link-color-hover;
            }
          }
        }
      }
    }
  }
}

@media(min-width: $desktop) {
  body {
    padding:2em;
    background-color: crimson;
    #root {
      .App {
        .albums-container {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          .album {
            border-bottom: none;
            height: 100%;
          }
        }
      }
    }
  }
}